import React from 'react';
import DisplayGrid from './displayGrid';

function getIndex(i,j,M){
    return i*M+j;
}
function getParent(parent,index){
    if(parent[index]==index){
        return index;
    }
    return getParent(parent,parent[index]);
}
function makeUnion(parent,i,j){
    const pi = getParent(parent,i);
    const pj = getParent(parent,j);
    if(pi>=pj){
        parent[pi] = pj; 
    }
    else{
        parent[pj]=pi;
    }
    return ;
}
class ComponentLabelling extends React.Component {
    constructor(props){
        super(props);
        console.log(this.props);
        console.log("Component Labelling Constructor Invoked");
        this.N=this.props.N;
        this.M=this.props.M;
        this.grid = this.props.grid;
        this.fPassArray = this.props.grid;
        this.sPassArray = this.props.grid;
        this.firstPass = this.firstPass.bind(this);
        this.secondPass = this.secondPass.bind(this);
        this.finalComponents = this.finalComponents.bind(this);
        this.totalComponents = 0;
    }
    firstPass(){
        this.totalComponents = 0;
        this.grid = this.props.grid;
        this.fPassArray = [...this.props.grid];
        const n = this.N;
        const m = this.M;
        for(var i = 0;i<n;++i){
            for(var j = 0;j<m;++j){
                if(this.grid[getIndex(i,j,m)]==0){
                    continue;
                }
                this.fPassArray[getIndex(i,j,m)] = 0; 
                const top = (i-1)>=0 ? this.grid[getIndex(i-1,j,m)]: 0 ;
                const left = (j-1)>=0 ? this.grid[getIndex(i,j-1,m)]: 0 ;
                if(top>0&&left>0){
                    this.fPassArray[getIndex(i,j,m)] = (this.fPassArray[getIndex(i,j-1,m)] < this.fPassArray[getIndex(i-1,j,m)]) ?this.fPassArray[getIndex(i,j-1,m)]:this.fPassArray[getIndex(i-1,j,m)];
                }
                else if(top>0){
                    this.fPassArray[getIndex(i,j,m)] = (this.fPassArray[getIndex(i-1,j,m)]);
                }
                else if(left>0){
                    this.fPassArray[getIndex(i,j,m)] = (this.fPassArray[getIndex(i,j-1,m)]);
                }
                else{
                    this.totalComponents = 1+this.totalComponents;
                    this.fPassArray[getIndex(i,j,m)] = this.totalComponents;
                }
            }
        }
        console.log('fPassArray out is',this.fPassArray);
    }
    secondPass(){
        const n = this.N;
        const m = this.M;
        this.grid = this.props.grid;
        this.sPassArray = [...this.props.grid];
        var parent = [];
        for(var x = 0; x < (this.totalComponents+1) ; ++x ){
            parent.push(x);
        }
        for(var i = 0;i<n;++i){
            for(var j = 0;j<m;++j){
                if(this.fPassArray[getIndex(i,j,m)]==0){
                    continue;
                }
                const top = (i-1)>=0 ? this.fPassArray[getIndex(i-1,j,m)]: 0 ;
                const left = (j-1)>=0 ? this.fPassArray[getIndex(i,j-1,m)]: 0 ;
                if(top>0){
                    makeUnion(parent,this.fPassArray[getIndex(i,j,m)],this.fPassArray[getIndex(i-1,j,m)]);
                }
                if(left>0){
                    makeUnion(parent,this.fPassArray[getIndex(i,j,m)],this.fPassArray[getIndex(i,j-1,m)]);
                }
            }
        }
        for(var I = 0;I<n;++I){
            for(var J = 0;J<m;++J){
                if(this.fPassArray[getIndex(I,J,m)]==0){
                    continue;
                }
                this.sPassArray[getIndex(I,J,m)] = getParent(parent,this.fPassArray[getIndex(I,J,m)]);
            }
        }
        this.parent = parent;
    }
    finalComponents(){
        const distinctParents = [...new Set(this.parent)]; 
        this.finalArray = [];
        const n = this.N;
        const m = this.M;
        for(var i = 0 ; i<n ; ++i ){
            var tmp = [];
            for(var j = 0; j<m ; ++j){
                tmp.push(boxedColor[distinctParents.indexOf(this.sPassArray[getIndex(i,j,m)])]);
                this.sPassArray[getIndex(i,j,m)] = distinctParents.indexOf(this.sPassArray[getIndex(i,j,m)]);
            }
            this.finalArray.push(tmp);
        }
        console.log("Final Array",this.finalArray);
    }
    render(){
        this.firstPass();
        this.secondPass();
        this.finalComponents();
        return (
        <>
        <DisplayGrid array2D = {this.fPassArray} N={this.N} M={this.M} heading={"FIRST PASS"}/>
        <DisplayGrid array2D = {this.sPassArray} N={this.N} M={this.M} heading={"SECOND PASS"}/>
        <DisplayGrid array2D = {this.sPassArray} colorArray = {this.finalArray} N={this.N} M={this.M} heading={"LABELLED COMPONENTS"}/>
        </>
        );
    }
}
export default ComponentLabelling;

