import React from 'react';
import Cell from './cell';
import ComponentLabelling from './componentLabelling';
import {Wrapper,StyledSpan} from '../styledcomponents';
var _ = require('lodash');

class grid extends React.Component{
    constructor(props){
        super(props);
        this.grid = [];
        var matrix = [];
        this.N = this.props.N;
        this.M = this.props.M;
        for(var row = 0; row < (this.N*this.M) ; row++) {
            matrix.push(0);
        }
        this.grid = [...matrix];
        this.row = new Array(this.N);
        this.col = new Array(this.M);
        this.handleClick = this.handleClick.bind(this);
        this.state = {reRender:'false'};
    }
    handleClick(i,j){
      this.grid[i*this.M+j] = (this.grid[i*this.M+j]^1);
      this.setState({reRender:!this.reRender});
    }
    render(){
        return (
            <>    
             <Wrapper
            padding={'40px'}
        >
        <Wrapper
        display='flex'
        justifyContent='center'
        >
            <StyledSpan 
                color='#b31020'
                fontSize='60px'
                fontWeight='600'
                bgColor='#EDECA5'
                padding={'20px'}
                margin={'20px'}
            >
                IMAGE
            </StyledSpan>
        </Wrapper>     
            {/* Initial Image  Created */}
            {_.map(this.row,(r,i) => {
                    return (
                    <Wrapper 
                        display='flex'
                        width='100%'
                        height='100%'
                        key={i}
                        justifyContent='center'
                    >
                    {_.map(this.col,(c,j) => {
                        return (
                            <Cell
                                width='40px'
                                height='40px'
                                border='5px solid #909090'
                                justifyContent='center'
                                action={this.handleClick}
                                i={i}
                                j={j}
                                value={this.grid[i*this.M+j]}
                                key={i*this.M+j}
                                textAlign='center'
                                verticalAlign='middle'
                            />
                        );
                                            }
                           )
                    }
                    </Wrapper>
                    );
                                            }
                  )
            }
            </Wrapper>
            {/* First Pass is Created <ComponentLabelling array2D={this.array2D}/>       */}
            <ComponentLabelling grid={this.grid} N={this.N} M={this.M} />     
        </>
        );
    }
    
}
export default grid;