
import React from 'react';
import Grid from './grid';

class body extends React.Component{    
    constructor(props)
    {
        super(props);
        this.N = 12;
        this.M = 12;
    }
    render(){
    return (
        <>
        <Grid N={this.N} M={this.M} />
        </> 
    );
}
}
export default body;