import React from 'react';
import {StyledSpan,Wrapper} from '../styledcomponents';
class Header extends React.Component{
  constructor(props){
    super(props);
  }
  render() {
    return (
      <Wrapper
      bgColor='#C9EAFC'
      color='#410CB5'
      fontSize='80px'
      fontWeight='800'
      textAlign={'center'}
      >
        COMPONENT LABELLING 
      </Wrapper>
    );
  }    
}
export default Header;