import Cell from './cell';
import {Wrapper,Button,StyledSpan} from '../styledcomponents';
var _ = require('lodash');

function DisplayGrid(props){
    const array2D = props.array2D;
    const row = new Array(props.N);
    const col = new Array(props.M);
    const N = props.N;
    const M = props.M;
    return (
        <>
        <Wrapper
            padding={'40px'}
        >
        <Wrapper
        display='flex'
        justifyContent='center'
        >
            <StyledSpan 
                bgColor='#370CD4'
                fontSize='60px'
                fontWeight='600'
                color='#F3F0FE'
                padding={'20px'}
                margin={'20px'}
            >
                {props.heading}
            </StyledSpan>
        </Wrapper>     
        {_.map(row,(array,i) => {
                return (
                <Wrapper 
                    display='flex'
                    width='100%'
                    height='100%'
                    key={i}
                    justifyContent='center'
                >
                {_.map(col,(arr,j) => {
                    return (
                        <Button
                            width='40px'
                            height='40px'
                            border='5px solid #909090'
                            justifyContent='center'
                            action={props.action}
                            key={i*M+j}
                            bgColor = {props.colorArray?props.colorArray[i][j]:'#FFFFFF'}
                            color = {props.color}   
                            textAlign='center'
                            verticalAlign='middle'
                            fontWeight='600'
                            fontSize='30px'
                        >
                        {array2D[i*M+j]}
                        </Button>
                    );
                                        }
                       )
                }
                </Wrapper>
                );
                                        }
              )
        } 
         </Wrapper>    
    </>
    );
}
export default DisplayGrid;